/* base */
*, *:before, *:after {
  box-sizing: border-box; }

html {
  line-height: 1.3;
  font-size: 100%;
  -webkit-tap-highlight-color: #007dff4f; }

html, body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px; }

body {
  background: #ffffff;
  font-size: 0.875rem;
  font-family: "IBM Plex Sans", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
  color: #222;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%; }
  body.is-fixed {
    overflow: hidden; }

.base {
  display: flex;
  flex-direction: column;
  height: 100%; }

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: #222;
