  margin-bottom: 16px;
  font-family: "Gilroy", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 800;
  color: #071739; }
  .--product-card .h1, .--product-card h1 {
    font-size: 1.25rem;
    line-height: 1.4;
    margin-bottom: 8px; }

.h2, h2 {
  font-size: 1.5rem;
