  line-height: 1.38; }

.h5, h5 {
  font-size: 0.8125rem; }

.h6, h6 {
  font-size: 0.75rem; }

small, .small {
  font-size: 0.75rem;
  line-height: 1.2; }

p {
  line-height: 1.6;
  margin: 0 0 16px 0; }

em, i, .em, .i {
  font-style: italic; }

strong, b, .strong, .b {
  font-weight: 700; }

hr {
  border: solid #ddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0; }

a, .link {
  color: #6C9BD7;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s, background 0.35s, border 0.35s; }
  a:hover, a:focus, a:active, .link:hover, .link:focus, .link:active {
    color: #6C9BD7;
    text-decoration: none;
    outline: none; }

a.link:hover, a.link:focus, a.link:active {
  text-decoration: none; }

img, .img {
  box-sizing: content-box;
  display: inline-block;
  vertical-align: middle; }

.container {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  padding: 0 16px; }
  
  .container-fluid,
  .container-sm,
  .container-md,
  .container-lg,
  .container-xl {
    padding: 0 16px; }

/* img */
.img-responsive, .img--responsive {
  display: block;
  height: auto;
  max-width: 100%; }

.img-round, .img--round {
  border-radius: 50%; }

/* section */
.section {
  padding: 0 0 40px 0;
  min-width: 320px; }

.container .section {
  min-width: inherit; }

ul {
  padding: 0;
  font-weight: 600; }
  ul li {
    list-style: none;
    padding-left: 24px;
    position: relative;
    line-height: 1.55;
    margin-bottom: 16px; }
    ul li::before {
      content: '';
      position: absolute;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      left: 6px;
      top: 5px;
      background: #6C9BD7; }

ol {
  padding: 0;
  counter-reset: section;
  font-weight: 500; }
  ol li {
    list-style: none;
    line-height: 1.55;
    margin-bottom: 8px; }
    ol li::before {
      counter-increment: section;
      content: counter(section) ". ";
      color: #274684;
      font-weight: 700; }

