/* breadcrumb */
.breadcrumbs {
  margin: 0;
  padding: 0 0 8px 0;
  font-size: 1rem;
  display: flex;
  flex-wrap: wrap;
  color: #8F8F8F;
  font-weight: 500; }
  .breadcrumbs__item {
    list-style: none;
    margin: 0; }
    .breadcrumbs__item + .breadcrumbs__item {
      margin-left: 10px; }
      .breadcrumbs__item + .breadcrumbs__item::before {
        content: '\e901';
        margin-right: 10px;
        font-family: "icomoon";
        color: #000;
        display: inline-block;
        font-weight: 400; }
    .breadcrumbs__item > a, .breadcrumbs__item > span {
      display: inline-block;
      line-height: 14px;
      text-decoration: none; }
    .breadcrumbs__item > a {
      color: #8F8F8F; }
    .breadcrumbs__item:first-child {
      font-weight: 700; }
    .breadcrumbs__item::before {
      display: none; }

/* buttons */
.btn {
  display: inline-block;
  padding: 15px 20px;
  margin-bottom: 0;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  vertical-align: top;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  background-image: none;
  background-color: #6C9BD7;
  border: 1px solid #6C9BD7;
  border-radius: 4px;
  color: #ffffff;
  line-height: 16px;
  text-decoration: none;
  transition: all 0.35s;
  position: relative;
  min-width: 80px;
  background-position: center;
  -webkit-tap-highlight-color: transparent; }
  .btn + .btn {
    margin-left: 15px; }
  .btn:hover, .btn:focus, .btn:active {
    outline: none; }
  .btn:hover {
    color: #ffffff;
    background-color: #6C9BD7;
    border-color: #6C9BD7;
    text-decoration: none; }
  .btn:active, .btn:focus {
    color: #ffffff;
    background-color: #6C9BD7;
    border-color: #6C9BD7;
    text-decoration: none; }
  @media (hover: none) and (pointer: coarse) {
    .btn {
      transition: background 0.5s; }
      .btn:hover {
        background: #6C9BD7 radial-gradient(circle, transparent 1%, #6C9BD7 0%) center/15000%; }
      .btn:active {
        background-color: #274684;
        background-size: 100%;
        transition: background 0s; } }
  .btn.--white {
    background: #ffffff;
    color: #222;
    border-color: #ffffff; }
    .btn.--white:hover, .btn.--white:active, .btn.--white:focus {
      background: #ffffff;
      color: #222;
      border-color: #ffffff; }
    @media (hover: none) and (pointer: coarse) {
      .btn.--white {
        transition: background 0.5s; }
        .btn.--white:hover {
          background: #ffffff radial-gradient(circle, transparent 1%, #ffffff 0%) center/15000%; }
        .btn.--white:active {
          background-color: #6C9BD7;
          background-size: 100%;
          transition: background 0s; } }
  @media (hover: none) and (pointer: coarse) {
    .btn {
      transition: background 0.5s; }
      .btn:hover {
        background: #6C9BD7 radial-gradient(circle, transparent 1%, #6C9BD7 0%) center/15000%; }
      .btn:active {
        background-color: #274684;
        background-size: 100%;
        transition: background 0s; } }
  .btn.--block {
    display: block;
    width: 100%; }
  .btn.--sm {
    font-size: 0.75rem;
    padding: 7px 10px;
    border-radius: 4px; }
    .btn.--sm [class*="icon-"] {
      margin-right: 5px; }
  .btn.--md {
    padding-top: 11px;
    padding-bottom: 11px;
    border-radius: 4px; }
  .btn.--lg {
    padding-top: 16px;
    padding-bottom: 16px; }
  .btn.--xl {
    padding-top: 23px;
    padding-bottom: 23px; }
  .btn.--two-line {
    padding-top: 4px;
    padding-bottom: 4px;
    line-height: 15px; }
  .btn.disabled {
    color: #071739 !important;
    background-color: #ffffff !important;
    border-color: #8F8F8F !important;
    cursor: auto !important;
    opacity: 1; }
    @media (hover: none) and (pointer: coarse) {
      .btn.disabled:hover {
        background: transparent; }
      .btn.disabled:active {
        background-color: transparent; } }
  .btn.--center {
    width: 100%;
    max-width: 230px;
    display: block;
    margin-left: auto !important;
    margin-right: auto !important;
    margin-bottom: 25px; }
  .btn.--height-auto {
