/* pagination */
.pagination {
  font-size: 1rem;
  text-align: left;
  margin-bottom: 50px; }
  .pagination ul {
    margin: 0;
    padding: 0;
    display: flex;
    width: 100%; }
    .pagination ul > li {
      list-style: none;
      margin: 0;
      display: inline-block;
      line-height: 1;
      vertical-align: middle; }
      .pagination ul > li.pagination__prev, .pagination ul > li.pagination__next {
        font-size: 1.25rem; }
        .pagination ul > li.pagination__prev > a, .pagination ul > li.pagination__prev > span, .pagination ul > li.pagination__next > a, .pagination ul > li.pagination__next > span {
          border-color: #8F8F8F; }
          .pagination ul > li.pagination__prev > a::before, .pagination ul > li.pagination__prev > span::before, .pagination ul > li.pagination__next > a::before, .pagination ul > li.pagination__next > span::before {
            font-family: "icomoon";
            vertical-align: -2px;
            line-height: 1;
            display: inline-block; }
      .pagination ul > li.pagination__prev {
        margin-right: 0px; }
        .pagination ul > li.pagination__prev > a::before, .pagination ul > li.pagination__prev > span::before {
          content: '\1f3d3'; }
      .pagination ul > li.pagination__next {
        margin-left: 8px; }
        .pagination ul > li.pagination__next > a::before, .pagination ul > li.pagination__next > span::before {
          content: '\1f3ee'; }
      .pagination ul > li + li {
        margin-left: 8px; }
      .pagination ul > li > a, .pagination ul > li > span {
        display: inline-block;
        width: 40px;
        height: 40px;
        text-align: center;
        line-height: 39px;
        color: #000;
        text-decoration: none;
        border-radius: 50%;
        border: 1px solid transparent;
        background: #ffffff;
        transform: translateY(0px);
        position: relative;
        z-index: 1;
        overflow: hidden;
        vertical-align: middle; }
      .pagination ul > li > a:hover {
        z-index: 2;
        background: #8F8F8F;
        color: #ffffff;
        border-color: #8F8F8F;
        transform: translateY(0px); }
      .pagination ul > li.active > a, .pagination ul > li.active > span {
        background: #8F8F8F;
        color: #ffffff;
        border-color: #8F8F8F; }
      .pagination ul > li.disabled {
        display: none; }
        .pagination ul > li.disabled > a, .pagination ul > li.disabled > span {
          cursor: auto;
          border-color: #8F8F8F;
          color: #8F8F8F;
          box-shadow: none;
          transform: translateY(0); }
  .pagination--center ul {
    justify-content: center; }
  .pagination--right ul {
    justify-content: flex-end; }
